}

.aic-translate-panel {
  background: var(--color-aic-bg-panel);
  border: 1px solid var(--color-aic-border-light);
  border-radius: var(--radius-sm);
  overflow: hidden;
}

.aic-translate-panel__label {
  padding: 6px 10px;
  font-size: var(--text-xs);
  font-weight: 600;
  color: var(--color-aic-text-secondary);
  background: var(--color-aic-bg);
  border-bottom: 1px solid var(--color-aic-border-light);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.aic-translate-panel__text {
  padding: 10px;
  font-size: var(--text-md);
  line-height: 1.6;
  color: var(--color-aic-text);
  max-height: 240px;
  overflow-y: auto;
}

.aic-translate-panel--target {
  border-color: rgba(59, 158, 255, 0.2);
}

.aic-translate-panel--target .aic-translate-panel__label {
  color: var(--color-aic-blue);
  background: var(--color-aic-blue-light);
}

/* ================================================================
   Spinner keyframes
   ================================================================ */

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes focusPulse {
  0%, 100% {
    box-shadow: 0 0 0 2px var(--color-aic-blue-glow);
  }
  50% {
    box-shadow: 0 0 0 4px var(--color-aic-blue-glow);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
